import React from "react";
import { Link } from "react-router-dom";
import "./Navigation.css";

function Navigation() {
    return (
        <div className="nav">
            <div className="links">
                <Link to="/">Home</Link> <br></br>
                <Link to="/recommend">추천할 수 박에</Link> <br></br>
                <Link to="/festival">축제/행사 소개</Link> <br></br>
                <Link to="/community">너와 나의 연결고리</Link> <br></br>
            </div>
            <Link to="/login" className="login-btn">로그인</Link>
        </div>
    );
}
export default Navigation;